.note-path-warp {
    position: absolute;
    top: 140px;
    left: 50%;
    transform: translateX(-50%);
    width: 268px;
    min-height: 100px;
    max-height: 168px;
    background: #ffffff;
    padding: 4px 12px;
    border: 1px solid #eceff4;
    box-sizing: border-box;
    box-shadow: 0px 4px 8px rgba(35, 45, 71, 0.1);
    border-radius: 4px;
    overflow: auto;
    z-index: 100;

    .note-book {
        display: flex;
        flex-direction: column;

        .note-book-item {
            display: flex;
            align-items: center;
            height: 32px;
            // margin-bottom: 10px;
            cursor: pointer;

            &:hover {
                background: #f4f6f7;

                p {
                    color: #5b89fe;
                }
            }
        }

        p {
            flex: 1;
            font-size: 14px;
            line-height: 22px;
            color: #4e5a70;
            margin-left: 8px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }

        .note-book-children {
            margin-left: 12px;
        }
    }
}
