.form-wrap {
    overflow: hidden;

    input {
        display: block;
        width: 268px;
        height: 32px;
        margin: 45px auto 24px;
        padding: 5px 12px;
        border: 1px solid #eceff4;
        box-sizing: border-box;
        border-radius: 5px;
        resize: none;
        outline: none;
        font-family: PingFang SC;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 32px;
        color: #4e5a70;

        &:focus {
            border: 1px solid #5b89fe;
        }
    }

    .clipper-type-wrap {
        display: flex;
        justify-content: space-between;
        width: 268px;
        margin: 28px auto 0;

        .clipper-type {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 81px;
            height: 81px;
            background: #f9fafb;
            border: 1px solid #c2d3ff;
            box-sizing: border-box;
            border-radius: 4px;
            position: relative;

            .radio {
                position: absolute;
                top: 5px;
                left: 5px;
            }

            p {
                margin-top: 5px;
                font-family: PingFang SC;
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 20px;
                color: #7a8599;
            }
        }

        .clipper-type-l {
            width: 128px;
        }

        .clipper-selected {
            background: #f5f8ff;
        }
    }

    .save-path {
        width: 268px;
        height: 32px;
        margin: 0 auto;
        padding: 0 8px;
        display: flex;
        align-items: center;
        border: 1px solid #eceff4;
        box-sizing: border-box;
        border-radius: 5px;

        .dir-save-name {
            flex: 1;
            margin-left: 4px;
            font-family: PingFang SC;
            font-size: 14px;
            color: #4e5a70;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }

    .save-btn {
        width: 268px;
        height: 40px;
        margin: 33px auto 0;
        background: #5b89fe;
        border-radius: 4px;
        font-weight: 500;
        font-size: 16px;
        line-height: 40px;
        text-align: center;
        color: #ffffff;
        cursor: pointer;
    }
}
