body,
html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background: #fff;
}

#root {
    width: 100%;
    height: 100%;
}

a {
    text-decoration: none;
}

p {
    margin: 0;
    padding: 0;
}

.toast {
    position: fixed;
    top: 50%;
    left: 50%;
    padding: 2px 4px;
    text-align: center;
    border-radius: 4px;
    font-size: 14px;
    background: #000;
    color: #fff;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
    z-index: 666;
}
