@keyframes load {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.loading-wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 888;
    text-align: center;
    background: #fff;

    svg {
        margin-top: 145px;
        animation: load 1s infinite linear;
    }

    p {
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        color: #7a8599;
        margin-top: 4px;
    }
}
