.login {
    width: 100%;
    overflow: hidden;
    text-align: center;

    .logo-wrap {
        width: 101px;
        height: 94px;
        margin: 62px auto 0;
        position: relative;

        .logo-jc {
            position: absolute;
            top: -10px;
            right: -15px;
        }
    }

    .logo-tips {
        margin: 40px 0;
        line-height: 161.5%;
        font-family: PingFang SC;
        font-size: 14px;
        color: rgba(38, 42, 51, 0.6);
    }

    .login-btn {
        display: block;
        width: 210px;
        height: 40px;
        margin: 0 auto 18px;
        background: #5383fe;
        box-shadow: 0px 4px 12px rgba(83, 131, 254, 0.36);
        border-radius: 6px;
        font-family: PingFang SC;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 40px;
        text-align: center;
        color: #ffffff;
    }

    .login-register {
        font-family: PingFang SC;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: #5383fe;
    }
}
