.success-wrap {
    svg {
        display: block;
        margin: 110px auto 0;
    }

    p {
        margin-top: 4px;
        text-align: center;
        font-family: PingFang SC;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        color: #4e5a70;
    }

    .skip-web {
        display: block;
        width: 268px;
        height: 40px;
        background: #5b89fe;
        margin: 83px auto 0;
        border-radius: 4px;
        font-family: PingFang SC;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 40px;
        text-align: center;
        color: #ffffff;
        cursor: pointer;
    }
}
