.user-wrap {
    width: 100%;
    height: 64px;
    position: absolute;
    bottom: 0;
    display: flex;
    background: #f9fafb;
    align-items: center;
    justify-content: space-between;

    .user-info {
        display: flex;
        flex: 1;
        align-items: center;
        margin-left: 20px;
        overflow: hidden;

        img {
            width: 32px;
            height: 32px;
            border-radius: 50%;
        }

        .user-name {
            font-family: PingFang SC;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            color: #7a8599;
            margin: 0 4px 0 8px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }
    }

    .feedback {
        font-family: PingFang SC;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        text-align: center;
        color: #5b89fe;
        margin-right: 20px;
        cursor: pointer;
    }

    .logout {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        width: 100px;
        height: 32px;
        background: #fff;
        left: 60px;
        top: -16px;
        border: 1px solid #eceff4;
        box-sizing: border-box;
        box-shadow: 0px 4px 8px rgba(35, 45, 71, 0.1);
        font-family: PingFang SC;
        border-radius: 4px;
        font-size: 14px;
        color: #4e5a70;
        cursor: pointer;
        span {
            margin-left: 4px;
        }
    }
}
